import React, { Fragment } from 'react'
import { useInputValue } from '../customHooks/useInputValue'

import { Container, Form, Row, Col, InputGroup, Button } from 'react-bootstrap'

export const ContactForm = () => {

    const name = useInputValue('');
    const secondName = useInputValue('')
    const email = useInputValue('');
    const description = useInputValue('');

    const inputSimple = {
        md: 12,
        lg: 6
    }

    const inputDouble = {
        md: 6,
        lg: 3
    }

    const InputName = () => {
        return (
            <Row className="mb-3 justify-content-center" >
                <Col {...inputDouble}>
                    <Form.Control placeholder="First name" {...name} />
                </Col>
                <Col {...inputDouble}>
                    <Form.Control placeholder="Second name" {...secondName} />
                </Col>
            </Row>
        )
    }

    const InputEmail = () => {
        return (
            <Row className="mb-3 justify-content-center" >
                <Col {...inputDouble}>
                    <Form.Control type="email" placeholder="Enter you email" {...email} />
                    <Form.Control.Feedback type="valid">Checked!</Form.Control.Feedback>
                </Col>
                <Col {...inputDouble}>
                    <InputGroup >
                        <InputGroup.Prepend>
                            <InputGroup.Text>@</InputGroup.Text>
                        </InputGroup.Prepend>
                        <Form.Control placeholder="any.com" />
                    </InputGroup>
                </Col>
            </Row>
        )
    }

    const InputDescription = () => {
        return (
            <Row className="mb-3 justify-content-center" >
                <Col {...inputSimple}>
                    <Form.Control as="textarea" rows={3} placeholder="Could be any reason" {...description} style={{ resize: "none" }} />
                </Col>
            </Row>
        )
    }

    return (

        <Container fluid className="mt-5 justify-content-center">
            <Form >
                {InputName()}
                {InputEmail()}
                {InputDescription()}
                <Row className="justify-content-center" >
                <Col {...inputSimple}>
                    <Button>Send</Button>
                </Col>
                </Row>
            </Form>
        </Container>
    )
}
