import React, { Fragment } from 'react'

import { Advert } from './Advert'
import { NavBar } from './NavBar'

export const Header = (props) => {

    const { isAdvert, ...rest } = props
    return (
        <Fragment>
            <NavBar />
            { isAdvert && <Advert mode={"success"} text={"Vas a poder encontrar todo lo que necesites aqui debajo"} textHead={"Bienvenido"} textFoot={"Espero que te guste el sitio"} />}
        </Fragment>
    )
}
