import React from 'react'
import { Alert } from 'react-bootstrap'

export const Advert = (props) => {

    const { mode, text, textHead, textFoot, ...rest } = props;
    return (
        <Alert variant={mode} >
            {textHead && <Alert.Heading>{textHead}</Alert.Heading>}
            <p>
                {text}
            </p>
            <hr />
            {textFoot &&
            <p className="mb-0">{textFoot}</p>}
        </Alert>
    )
}
